import React from "react";
import {OraModal, OraButton} from "eshop/components/OraCommonComponents";

const MultiCartRemovePopup = (props) =>

            <OraModal id={props.id} showClose={false} narrow={true} fallback={props.onClickRemove}>
                <div className="l-page-row u-font-bold opl-msg opl-msg--info g-icon--xs opl-msg--context">
                    <div className="l-row u-spacing">
                        <div className="l-col l-col-12">
                            <p>{ props.bundleNo != null ? props.removeItemPopupText : props.removeCartPopupText }</p>
                        </div>
                    </div>
                    <div className="l-row">
                        <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 large-offset-by-4 u-small-spacing">
                            <OraButton onClick={props.onClickRemove} type="secondary" className="u-w-100" modal={true}>
                                { props.bundleNo != null ? props.removeItemAgreeButtonText : props.removeCartAgreeButtonText }
                            </OraButton>
                        </div>
                        <div className="l-col l-col-small-12 l-col-medium-6 l-col-4">
                            <OraButton className="u-w-100" modal={true}>
                                { props.bundleNo != null ? props.removeItemNotAgreeButtonText : props.removeCartNotAgreeButtonText }
                            </OraButton>
                        </div>
                    </div>
                </div>
            </OraModal>;

MultiCartRemovePopup.defaultProps = {
    removeItemPopupText: "Czy na pewno chcesz usunąć produkt z koszyka?",
    removeItemAgreeButtonText: "Tak",
    removeItemNotAgreeButtonText: "Nie",
    removeCartPopupText: "Czy na pewno chcesz usunąć wszystko z koszyka?",
    removeCartAgreeButtonText: "Tak",
    removeCartNotAgreeButtonText: "Nie"
};

export default MultiCartRemovePopup;
